"use client";

import * as React from "react";
import * as LabelPrimitive from "@radix-ui/react-label";
import { motion } from "framer-motion";
import { cn } from "@/lib/utils";

const MotionLabel = motion.create(LabelPrimitive.Root);

const Label = React.forwardRef(({ className, ...props }, ref) => {
  return (
    <MotionLabel
      ref={ref}
      className={cn(
        "text-sm font-medium leading-none tracking-wide peer-disabled:cursor-not-allowed peer-disabled:opacity-70",
        "text-gray-300 bg-gradient-to-r from-gray-200 to-gray-400 bg-clip-text text-transparent",
        className
      )}
      whileHover={{ scale: 1.01 }}
      transition={{ duration: 0.2 }}
      {...props}
    />
  );
});

Label.displayName = "Label";

export { Label };
